import DropDownList from './DropDownList'

function MonthSelector({ currentMonth, monthSetter }) {

  return (

    <DropDownList
      itemType="month"
      items={months}
      currentItem={currentMonth}
      itemSetter={monthSetter}
    />
  )
}

const months = [
  
  "Janur",
  "Februr",
  "Mrcius",
  "prilis",
  "Mjus",
  "Jnius",
  "Jlius",
  "Augusztus",
  "Szeptember",
  "Oktber",
  "November",
  "December"
]

export default MonthSelector
export { months }