import '../styles/CrisisAccommodation.css'
import FramedLabel from './FramedLabel'
import House from './image_components/House'

function CrisisAccommodation() {

  return (

    <div className="crisis-accommodation">

        <FramedLabel text="Krzis Szll"/>

        <div className="crisis-accommodation-container">

            <p>
                A trsashz ngy laksos.<br/>
                Emeletenknt 2-2 lakssal. Berendezve btorokkal, alap hztartsi eszkzkkel (mosgp, ht, mikr, tzhely)<br/>
                A fszt/1. laks 66nm 4 frfi kollgnk elhelyezse megoldhat itt. (2 egyszemlyes, 1 ktszemlyes szoba)<br/>
                A fszt/2. laks hlgyek ltal lakott. 54,3 nm, 3 frhelyes. (1 egyszemlyes, 1 ktszemlyes szoba)<br/>
                Az I/3. laks szintn ni rsz, 69nm, 4 frhelyes. (2 egyszemlyes, 2 ktszemlyes szoba)<br/>
                Az I/4. laks szintn ni, 72, 5nm, 4 frhelyes. (Ebben a laksban most lakik egy nem-lt kollgan vakvezet munkakutyval s egy egyedlll anyuka a kislnyval egyszemlyes szobkban + 2 kollgan a ktszemlyes szobban.)
                15 lak elhelyezsre van lehetsg a Jvorka utcai Krzis szlln.<br/>
                Kln egyszemlyes szobban 7 f: 23 160 Ft/f havonta.<br/>
                Ktszemlyes szobban 8 f: 19 300 Ft/f havonta + rezsi.<br/>
                A laksok kln mrrkkal felszereltek, a lakk kzsen fizetik az elhasznlt vz, gz s ram utn a szmlkat.<br/>
            </p>

            <House/>

        </div>

    </div>
  )
}

export default CrisisAccommodation